// ------------------------------
// Style Guide
// ------------------------------

html {
  @include font-smoothing(true);
  line-height: 1.5;
}

body {
  padding-bottom: 4rem;
}

// Fonts
p,
ol,
ul {
  font-family: serif;
}

// Vertical Rhythm
// https://github.com/inuitcss/inuitcss/blob/04abcb2cb1285a3223b23f3ef28790222e9ef906/generic/_generic.shared.scss
address,
h1, h2, h3, h4, h5, h6,
blockquote, p, pre,
dl, ol, ul,
figure,
table,
fieldset {
  margin-bottom: 1rem;
}

main h1:first-child {
  margin-top: 3rem;
}

h2,
h3 {
  margin-top: 2rem;
}

// Dividers
hr {
  margin: 4rem auto;
}
h1.styleguide:not(:first-child) {
  margin-top: 4rem;
  padding-top: 4rem;
  border-top: 1px solid;
}

// Lists
ol.styleguide,
ul.styleguide {
  padding-left: 2rem;
}
ol.styleguide {
  list-style: decimal;
}
ul.styleguide {
  list-style: disc;
}

// Links / Buttons
a,
button {
  transition: opacity 100ms ease-in-out;

  @include hover('is-active') {
    opacity: 0.8;
  }
}

// Code Examples
code {
  display: inline-block;
  max-width: calc(100% - 1em);
  padding: 0 0.25rem;
  background: $color-gray-4;
  overflow: hidden;
  vertical-align: bottom;
  text-overflow: ellipsis;
}

// .codeExample
//   .exampleOutput
//   .codeBlock
//     .highlight
.codeBlock + h1 {
  margin-top: 2rem;
}
.codeBlock {
  background: $color-gray-4;
  margin-bottom: 1rem;
  padding: 0.5rem;

  :last-child {
    margin-bottom: 0;
  }
}
.exampleOutput + .codeBlock {
  margin-top: 1rem;
}
.highlight {
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
// HACK: Remove margin from `.o-section` example to avoid cluttering with extra classes
.codeExample h4 {
  margin-bottom: 0;
}

// Todos
.todo {
  font-weight: bold;
  color: $color-red-1;
}



// ------------------------------
// Style Guide Layout
// ------------------------------

$container-width : 640;
$sidebar-width   : 192;

@include add-breakpoint('sidebar', em($container-width, 16) + (2 * em($sidebar-width, 16)));

@include o-list--inline('lt-sidebar');

// Layout
// .c-layout {
// }

// Wrapper
.c-layout__wrapper {
  max-width: rem($container-width);
  margin-right: auto;
  margin-left: auto;
  padding-right: $gutter-xs;
  padding-left: $gutter-xs;
  overflow: hidden;

  @include mq('sm') {
    padding-right: $gutter-sm;
    padding-left: $gutter-sm;
  }
}

// Sidebar
.c-layout__sticky-sidebar {
  display: block;
  position: sticky;
  top: 0;
  margin: 0 $gutter-xs;

  @include mq('lt-sidebar') {
    @include scroll();
    padding: 1rem;
    background: $color-gray-4;
    z-index: 1; // Keep above `.highlight` on iOS
  }

  @include mq('btwn-sm-and-sidebar') {
    .c-layout__sticky-sidebar {
      margin: 0 $gutter-sm;
    }
  }
}

@include mq('sidebar') {
  .c-layout__wrapper {
    padding-right: $gutter-sm;
    padding-left: $gutter-sm;
  }
  .c-layout__sticky-sidebar {
    top: 0;
    left: calc(50vw + 20rem); // 1/2 page width + 1/2 wrapper width
    width: rem($sidebar-width);
    height: 0;
    margin: 0 auto;
    transform: translateY(3rem);
  }
}

.c-sticky-sidebar__nav {
  margin-bottom: 0;
  list-style: none;

  @include mq('lt-sidebar') {
    display: inline-block;
    white-space: nowrap;
  }

  @include mq('sidebar') {
    @include scroll();
    position: relative;
    max-height: calc(100vh - 8rem);
    padding: 1rem;
    background: $color-gray-4;
  }
}

// Utilities
@include u-margin('right-xs', $gutter-xs, 'lt-sidebar');
@include u-text-align('center', 'lt-sidebar');

// TODO: Make scroll utilities (like `object-fit`)
.u-scroll {
  @include scroll();
}
.u-scroll-disabled {
  @include scroll-disabled();
}



